//   
export interface BasePortfolioGroup {
  name: string;
}

export interface BasePortfolio {
  groupId: number;
  broker: string;
  accountNumber: string;
  accountName: string;
  currency: "KRW" | "USD";
}

export interface BasePosition {
  portfolioId: number;
  symbol: string;
  quantity: number;
  avgPrice: number;
  currentPrice: number;
  tradeDate: string;
  strategyCategory: PortfolioCategory;
  strategyTags?: string[];
}

export interface BaseTodo {
  portfolioId: number | null;
  content: string;
  done: boolean;
  createdAt: string;
}

// DB   
export interface PortfolioGroup {
  id: number;
  name: string;
  config?: PortfolioGroupConfig;
}

export interface Portfolio {
  id: number;
  groupId: number;
  name?: string;
  broker: string;
  accountNumber: string;
  accountName: string;
  currency: "KRW" | "USD";
  config?: PortfolioConfig;
  positions?: Position[];
}

export interface Position {
  id: number;
  portfolioId: number;
  symbol: string;
  name: string;
  quantity: number;
  avgPrice: number;
  currentPrice: number;
  tradeDate: number;
  strategyCategory: PortfolioCategory;
  strategyTags: string[];
  category?: PortfolioCategory;
  strategy?: string;
  entryCount?: number;
  maxEntries?: number;
  targetQuantity?: number;
}

export interface Todo {
  id: number;
  portfolioGroupId: number;
  text: string;
  content?: string;
  completed: boolean;
  done?: boolean;
  createdAt: number;
  completedAt?: number | null;
}

//   
export interface StrategyConfig {
  targetAllocation: number;      //      (%)
  riskLevel: number;            //  (1-5)
  rebalancingPeriod?: string;   //  
  categoryAllocations: {
    [key in PortfolioCategory]: CategoryConfig;
  };
}

export interface CategoryConfig {
  targetPercentage: number;      //   (%)
  maxStockPercentage: number;    //    (%)
  maxEntries: number;            //    
}

export interface PortfolioConfig {
  totalCapital: number;
  categoryAllocations: Record<PortfolioCategory, CategoryAllocation>;
}

export interface PortfolioGroupConfig {
  targetAllocation: number;
  riskLevel: number;
  categoryAllocations: Record<PortfolioCategory, CategoryAllocation>;
}

//  
export enum PortfolioCategory {
  LONG_TERM = 'LONG_TERM',
  GROWTH = 'GROWTH',
  SHORT_TERM = 'SHORT_TERM',
  CASH = 'CASH',
  UNCATEGORIZED = 'UNCATEGORIZED'
}

export interface CategoryAllocation {
  targetPercentage: number;
  maxStockPercentage: number;
  maxEntries: number;
}

//    
export interface ConsolidatedPosition {
  symbol: string;
  name: string;
  totalQuantity: number;
  weightedAvgPrice: number;
  currentPrice: number;
  positions: Position[];
}

export interface StrategySnapshot {
  groupId: number;
  name: string;
  totalValue: number;
  targetValue: number;
  deviation: number;
  positions: ConsolidatedPosition[];
}

export interface BrokerSnapshot {
  broker: string;
  totalValue: number;
  portfolios: Portfolio[];
  positions: Position[];
}

//     
export type NewPortfolioGroup = Omit<PortfolioGroup, 'id'>;
export type NewPortfolio = Omit<Portfolio, 'id'>;
export type NewPosition = Omit<Position, 'id'>;

//     
export interface Memo {
  id: number;
  title: string;
  content: string;
  createdAt: number;
  updatedAt: number;
}

export type NewMemo = Omit<Memo, 'id'>;

// Todo   
export type NewTodo = Omit<Todo, 'id'>; 